<template>
    <main class="app-main">
        <section class="top">
            <p class="title"><md-icon class="go-back" name="arrow-left" size="lg" @click="goBack"></md-icon>成果转化</p>
        </section>
        <section class="middle">
            <div class="md-example-child md-example-child-tabs md-example-child-tab-bar-6 top-tab-bar">
                <md-tab-bar
                        v-model="current"
                        :items="items"
                        :has-ink="false"
                        @change="onTabChange"
                ></md-tab-bar>
                <md-swiper
                        :dragable="false"
                        ref="swiper"
                        :autoplay="0"
                        :is-prevent="false"
                        :is-loop="false"
                        :has-dots="false"
                        :has-ink="false"
                        @before-change="onSwiperChange">
                    <md-swiper-item>
                        <div class="item-cover">
                            <div class="title" :title="detail.title" v-html="detail.title"></div>
                            <div class="content">
                                <section v-html="detail.article">
                                </section>
                                <section class="img">
                                    <img :src="detail.coverPicture" alt="">
                                </section>
                                <section v-html="detail.summary">
                                </section>
                            </div>
                        </div>
                    </md-swiper-item>
                    <md-swiper-item>
                        <div class="item-cover">
                            <div class="title" :title="detail.title" v-html="detail.title"></div>
                            <div class="content">
                                <section v-html="detail.article">
                                </section>
                                <section class="img">
                                    <img :src="detail.coverPicture" alt="">
                                </section>
                                <section v-html="detail.summary">
                                </section>
                            </div>
                        </div>
                    </md-swiper-item>
                    <md-swiper-item>
                        <div class="item-cover">
                            <div class="title" :title="detail.title" v-html="detail.title"></div>
                            <div class="content">
                                <section v-html="detail.article">
                                </section>
                                <section class="img">
                                    <img :src="detail.coverPicture" alt="">
                                </section>
                                <section v-html="detail.summary">
                                </section>
                            </div>
                        </div>
                    </md-swiper-item>
                    <md-swiper-item>
                        <div class="item-cover">
                            <div class="title" :title="detail.title" v-html="detail.title"></div>
                            <div class="content">
                                <section v-html="detail.article">
                                </section>
                                <section class="img">
                                    <img :src="detail.coverPicture" alt="">
                                </section>
                                <section v-html="detail.summary">
                                </section>
                            </div>
                        </div>
                    </md-swiper-item>
                </md-swiper>
            </div>
        </section>
    </main>
</template>

<script>
import { getDetail } from '@/apiUrl/result'
import { TabBar, Swiper, SwiperItem, ScrollView, ScrollViewMore, Icon, Toast } from 'mand-mobile'
export default {
  name: 'News',
  components: {
    [TabBar.name]: TabBar,
    [Swiper.name]: Swiper,
    [SwiperItem.name]: SwiperItem,
    [ScrollView.name]: ScrollView,
    [ScrollViewMore.name]: ScrollViewMore,
    [Icon.name]: Icon,
    [Toast.name]: Toast
  },
  data () {
    return {
      // 当前选中tab
      current: 0,
      // tab项
      items: [{ name: 0, label: '概念验证', current: 52 }, { name: 1, label: '中试熟化', current: 53 }, { name: 2, label: '成果承接', current: 54 }, { name: 3, label: '投融资', current: 55 }],
      listLeft: 10,
      isFinished: false,
      Height: '900px',
      listRight: 10,
      currentId: 52,
      detail: {}
    }
  },
  mounted () {
    this.Height = window.innerHeight - 84 + 'px'
    this.getDetail()
  },
  methods: {
    getDetail () {
      getDetail({ id: this.currentId }).then(res => {
        this.detail = res.data
      }).catch(err => {
        Toast.failed(err)
      })
    },
    onSwiperChange (from, to) {
      console.log(from)
      this.current = to
    },
    onTabChange (item, index) {
      this.currentId = item.current
      this.getDetail()
      this.$refs.swiper.goto(index)
    },
    // 跳转新闻详情页
    $_onItemClick (i) {
      this.$router.push('/news/detail')
    },
    $_onEndReached () {
      if (this.isFinished) {
        return
      }
      // async data
      setTimeout(() => {
        this.listLeft += 10
        if (this.listLeft >= 15) {
          this.isFinished = true
        }
        this.$refs.scrollView.finishLoadMore()
      }, 1000)
    },
    $_onScroll ({ scrollLeft, scrollTop }) {
      // console.log(`[Mand Mobile ScrollView - demo0 - onScroll] scrollLeft: ${scrollLeft}, scrollTop: ${scrollTop}`)
    },
    // 回退
    goBack () {
      this.$router.push('/home')
    }
  }
}
</script>

<style lang="stylus" scoped>
    .middle
        position absolute
        top 88px
        width 100%
        .item-cover
            width 100%
            background-color rgba(166, 166, 166, 0.1)
            border-radius 12px
            margin 30px auto
            .title
                margin 0 30px
                height 85px
                line-height 85px
                font-size 32px
                color #333333FF
                border-bottom 1px solid #EEEEEE
                overflow hidden
                text-overflow ellipsis
                white-space nowrap
            .content
                padding-bottom 15px
                section
                    margin 30px
                    text-align left
                    text-indent 40px
                    color rgba(102,102,102,0.8)
                    span
                        font-weight bold
                        color #333333FF
                .img
                    text-indent 0
                    img
                        width 100%!important
            .content >>> img
                    max-width 630px!important
    .md-example-child-tab-bar-6 .md-swiper
        background #FFF
        .md-swiper-item
            padding 20px
            box-sizing border-box
            font-size 24px
            line-height 1.5
            color #666
    .scroll-view-item
        margin-bottom 30px
        text-align center
        font-size 28px
        font-family DINAlternate-Bold
        height 174px
    .scroll-view-container
        div
            display flex
            justify-content space-between
            img
                width 260px
            .article
                width 410px
                text-align left
                margin 10px 0
                display flex
                flex-direction column
                justify-content space-between
                p
                    color #333333
                    font-size 28px
                    font-weight Medium
                span
                    display flex
                    align-items center
                    line-height 24px
                    font-size 24px
                    img
                        width 24px
                        margin-right 10px
        .md-scroll-view-more
            justify-content center
    /deep/.top-tab-bar .md-tab-bar .md-tab-bar-item
        width 25%!important
    /deep/.top-tab-bar .md-tab-bar .md-tab-bar-item:nth-child(2).is-active
        background url("../../assets/news/common_tab_s_03@2x.png") no-repeat!important
        background-size 100% 100%!important
</style>
