import request from '../utils/request'
import apiPath from './url'
import qs from 'qs'
import store from '@/store/index'

export function getDetail (data) {
  const url = store.getters.status ? apiPath.result.detail : apiPath.result.getDetailWithoutToken
  data = qs.stringify(data)
  return request({
    url: url,
    method: 'post',
    data: data
  })
}
